.edit-button_type_photo {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;

  border: none;
  width: 100%;
  height: 100%;
  background-size: 26px 26px;
  opacity: 0;
}

.edit-button_type_photo:hover {
  opacity: .8;
  background-size: 26px 26px;
  background-color: rgba(0, 0, 0);
}